.App {
    background-color:#283444;
    width: fit-content;
    padding: 10px;
    color: white;
    border: white 0.5px solid;
    border-radius: 10px;
    margin-bottom: 40px;
    margin-left: 450px;
  }
  
  .autor {
    display: flex;
    flex-direction: row;
  }
  
  .author-img {
    height: 50px;
    border-radius: 50%;
  }
  
  .aurhor-name {
    margin: auto 10px;
    font-size: 17px;
    font-weight: 900;
  }
  
  .verified {
    height: 25px;
    padding-top: 14px;
  }
  
  .content {
    padding-left: 50px;
  }
  
  .content-img {
    height: 400px;
    border-radius: 10px;
  }
  
  .info {
    color: gray;
    font-weight: 700;
    margin: auto 4px;
    font-size: 12px;
  }
  
  .reactions {
    display: grid;
    grid-template-columns: repeat(4, 178px)
  }
  
  .react {
    text-align: center;
    font-size: 14px;
  }
  
  .icon {
    height: 12px;
    margin-right: 4px;
  }
  
  button {
    background-color: #283444;
    border: none;
    color: white;
  }
  
  